





















import Vue from "vue";
export default Vue.extend({
  name: "decisionReport",
  data() {
    return {
      selected: 0,
      options: [
        {
          value: 0,
          text: "颱風版",
          url: "https://yliflood.yunlin.gov.tw/Warning/Typhoon.jpg"
        },
        {
          value: 1,
          text: "豪雨版",
          url: "https://yliflood.yunlin.gov.tw/Warning/HeavyRain.jpg"
        }
      ]
    };
  },
  methods: {
    downloadReport() {
      var a = document.createElement("a");
      a.href = this.options[this.selected].url;
      a.download = "report.jpg";
      a.click();
    }
  }
});
